	private async initTouchedEvents() {

		if(!this.player) return

		let duration = 0
		await this.player.one("loadedmetadata", () => {})
		duration = this.player.duration()
		let forwading_time = duration < 45 ? 5 : 15

		if (this.mobileDetectMob()) {
			let forward_button = `
									<i class="vjs-forward-custom fas fa-redo" aria-hidden="true"></i>
								`
			let rewind_button = `
									<i class="vjs-rewind-custom fa fa-undo" aria-hidden="true"></i>
								`

			let vjs_big_play_button = this.player.el_.querySelector('.vjs-big-play-button')

			vjs_big_play_button.setAttribute('mobilebutton', true)

			var el = document.createElement("div");

				el.innerHTML = rewind_button;
				el.id = 'vjs-rewind-button'

				el.addEventListener('touchend', () => {
					this.player.currentTime((this.player.currentTime() - forwading_time) < 0 ? 0 : (this.player.currentTime() - forwading_time));
				})

			this.player.el_.insertBefore(el, vjs_big_play_button)

			var el = document.createElement("div");
				el.innerHTML = forward_button;
				el.id = 'vjs-forward-button'

				el.addEventListener('touchend', () => {
					this.player.currentTime(this.player.currentTime() + forwading_time);
				})

			this.insertAfter(vjs_big_play_button, el);

			this.player.bigPlayButton.disable()

			///let flag = false
			this.player.el_.addEventListener('touchend', (e: any) => {
				//
				if (e.target.parentElement && e.target.parentElement.classList && e.target.parentElement.classList.contains('vjs-big-play-button')) {

					if(!this.player.paused()) {
						this.player.pause();
					} else {
						this.player.play();
					}
					
				}
			})
		}

		if (this.player.el_.classList.contains('vjs-youtube')) {
			this.player.el_.querySelector('iframe').style.pointerEvents = 'none'
		}

		
	}

	private async buildVideoPlayerContributos(
		videoId: string
	) {

		/* @ts-ignore */
		await import("videojs-youtube")


		if (this.player) {




			try {
				this.player.dispose();
			}
			catch (e) {

			}
		}

		this.playerElement = document.createElement("video");
		this.playerElement.className = "video-js video-js-contributor";
		this.playerElement.setAttribute("playsinline", "true");
		this.playerElement.setAttribute("webkit-playsinline", "true");

		if (this.contributor == 'youtube') {

			var setupYoutube = {
				techOrder: ["youtube"],
				sources: [{ "type": "video/youtube", "src": "https://www.youtube.com/watch?v=" + videoId }],
				youtube: { "iv_load_policy": 1 }
			}

			this.playerElement.setAttribute("data-setup", JSON.stringify(setupYoutube));

			var paddingSize = 37.5

			this.playerElement.style.cssText = `padding-top: ${paddingSize}%; padding-bottom: ${paddingSize}%; `;

		}

		if (this.contributor == 'vimeo') {

			var setupVimeo = {
				techOrder: ["vimeo"],
				sources: [{ "type": "video/vimeo", "src": "https://vimeo.com/" + videoId }],
				vimeo: { color: "#fbc51b" }
			}

			this.playerElement.setAttribute("data-setup", JSON.stringify(setupVimeo));

		}

		this.wrapperElement.innerHTML = "";

		this.wrapperElement.appendChild(this.playerElement);

		const options: VideoJsPlayerOptions = {

			autoplay: false,
			controls: this.controls,
			muted: true,
			loop: this.loop,
			language: this.language
		};

		this.player = await PeertubePlayerManager.initializeContributor(
			this.playerElement,
			options
		);



		this.player.on("customError", (event: any, data: any) =>
			this.handleError(data.err)
		);

		this.player.on("error", (error: any) =>
			console.log(error)
		);

		this.player.tech().on("error", (error: any) =>
			console.log(error)
		);

		this.initializeApi();

		this.removePlaceholder();

		this.initTouchedEvents()
	}




	private removePlaceholder() {
		const placeholder = this.getPlaceholderElement();
		if (placeholder) placeholder.style.display = "none";
	}

	private getPlaceholderElement() {
		return document.getElementById("placeholder-preview");
	}

	/* T */

	private setHeadersFromTokens() {
		this.headers.set(
			"Authorization",
			`${this.userTokens.tokenType} ${this.userTokens.accessToken}`
		);
	}

	private removeTokensFromHeaders() {
		this.headers.delete("Authorization");
	}

