export type PlayerMode = "webtorrent" | "p2p-media-loader" | 'localvideo';

export type WebtorrentOptions = {
  videoFiles: VideoFile[];
};

export type P2PMediaLoaderOptions = {
  playlistUrl: string;
  segmentsSha256Url: string;
  trackerAnnounce: string[];
  redundancyBaseUrls: string[];
  videoFiles: VideoFile[];
  useP2P : boolean;
};

export interface CustomizationOptions {
  startTime: number | string;
  stopTime: number | string;

  controls?: boolean;
  muted?: boolean;
  loop?: boolean;
  subtitle?: string;
  resume?: string;

  pocketnetLink: string;
  logoType: string;
}

export interface CommonOptions extends CustomizationOptions {
  playerElement: HTMLVideoElement;
  onPlayerElementChange: (element: HTMLVideoElement) => void;

  autoplay: boolean;

  nextVideo?: () => void;
  hasNextVideo?: () => boolean;

  previousVideo?: () => void;
  hasPreviousVideo?: () => boolean;

  playlist?: PlaylistPluginOptions;

  videoDuration: number;
  enableHotkeys: boolean;
  inactivityTimeout: number;
  poster: string;

  theaterButton: boolean;
  captions: boolean;

  videoViewUrl: string;
  embedUrl: string;
  embedTitle: string;

  isLive: boolean;

  language?: string;

  videoCaptions: VideoJSCaption[];

  videoUUID: string;

  userWatching?: UserWatching;

  serverUrl: string;

  sources: any[];
}

export type PeertubePlayerManagerOptions = {
  common: CommonOptions;
  webtorrent: WebtorrentOptions;
  p2pMediaLoader?: P2PMediaLoaderOptions & { localTransport?: typeof fetch };
  error?: any;
};

export class PeertubePlayerManager {
  private static alreadyPlayed = false;

  static initState() {
    PeertubePlayerManager.alreadyPlayed = false;
  }

  static async initialize(
    mode: PlayerMode,
    options: PeertubePlayerManagerOptions,
  ) {
    let p2pMediaLoader: any;

    if (mode === "webtorrent") await import("./webtorrent/webtorrent-plugin");
    if (mode === "p2p-media-loader") {
      [p2pMediaLoader] = await Promise.all([
        import(
          "./p2p-media-loader/core/p2p-media-loader-master/p2p-media-loader-hlsjs/lib"
        ),
        import("./p2p-media-loader/p2p-media-loader-plugin"),
      ]);
    }

    const videojsOptions = this.getVideojsOptions(
      mode,
      options,
      p2pMediaLoader
    );

    const self = this;

    return new Promise((res) => {
      videojs(
        options.common.playerElement,
        videojsOptions,
        function (this: videojs.Player) {
          const player = this;

          player.one("play", () => {
            PeertubePlayerManager.alreadyPlayed = true;
          });

          self.addContextMenu(
            mode,
            player,
            options.common.embedUrl,
            options.common.embedTitle
          );

          player.bezels();

          return res(player);
        }
      );
    });
  }

  static async initializeContributor(
    el: HTMLElement,
    options: VideoJsPlayerOptions
  ) {
    // await TranslationsManager.loadLocaleInVideoJS(options.common.serverUrl, options.common.language, videojs)

    return new Promise((res) => {
      videojs(el, options, function (this: videojs.Player) {
        const player = this;

        player.one("play", () => {
          PeertubePlayerManager.alreadyPlayed = true;
        });

        /*player.bezels()*/

        return res(player);
      });
    });
  }

  private static getVideojsOptions(
    mode: PlayerMode,
    options: PeertubePlayerManagerOptions,
    p2pMediaLoaderModule?: any
  ): videojs.PlayerOptions {
    const commonOptions = options.common;
    const isHLS = mode === "p2p-media-loader";

    let autoplay = this.getAutoPlayValue(commonOptions.autoplay);
    let html5 = {
      preloadTextTracks: false,
    };

    const plugins: VideoJSPluginOptions = {
      peertube: {
        mode,
        autoplay, // Use peertube plugin autoplay because we could get the file by webtorrent
        videoViewUrl: commonOptions.videoViewUrl,
        videoDuration: commonOptions.videoDuration,
        userWatching: commonOptions.userWatching,
        subtitle: commonOptions.subtitle,
        videoCaptions: commonOptions.videoCaptions,
        stopTime: commonOptions.stopTime,
        isLive: commonOptions.isLive,
        videoUUID: commonOptions.videoUUID,
      },
    };

    if (commonOptions.playlist) {
      plugins.playlist = commonOptions.playlist;
    }

    if (commonOptions.enableHotkeys === true) {
      PeertubePlayerManager.addHotkeysOptions(plugins);
    }

    if (isHLS && options.p2pMediaLoader) {
      const { hlsjs } = PeertubePlayerManager.addP2PMediaLoaderOptions(
        plugins,
        options,
        p2pMediaLoaderModule
      );

      Object.assign(html5, hlsjs.html5);
    }

    if (mode === "webtorrent") {
      PeertubePlayerManager.addWebTorrentOptions(plugins, options);

      // WebTorrent plugin handles autoplay, because we do some hackish stuff in there
      autoplay = false;
    }

    

    const videojsOptions = {
      html5,

      // We don't use text track settings for now
      textTrackSettings: false as any, // FIXME: typings
      controls:
        commonOptions.controls !== undefined ? commonOptions.controls : true,
      loop: commonOptions.loop !== undefined ? commonOptions.loop : false,

      muted:
        commonOptions.muted !== undefined ? commonOptions.muted : undefined, // Undefined so the player knows it has to check the local storage

      autoplay: this.getAutoPlayValue(autoplay),

      poster: commonOptions.poster,
      inactivityTimeout: commonOptions.inactivityTimeout,
      playbackRates: [0.5, 0.75, 1, 1.25, 1.5, 1.75, 2],

      plugins,

      sources : commonOptions.sources || null,

      controlBar: {
        children: this.getControlBarChildren(mode, {
          captions: commonOptions.captions,
          pocketnetLink: commonOptions.pocketnetLink,
          logoType: commonOptions.logoType,
          theaterButton: commonOptions.theaterButton,

          nextVideo: commonOptions.nextVideo,
          hasNextVideo: commonOptions.hasNextVideo,

          previousVideo: commonOptions.previousVideo,
          hasPreviousVideo: commonOptions.hasPreviousVideo,
          resolutionMenuButton : commonOptions.sources ? false: true
        }) as any, // FIXME: typings
      },
    };

    if (commonOptions.language && !isDefaultLocale(commonOptions.language)) {
      Object.assign(videojsOptions, { language: commonOptions.language });
    }


    return videojsOptions;
  }

  private static addP2PMediaLoaderOptions(
    plugins: VideoJSPluginOptions,
    options: PeertubePlayerManagerOptions,
    p2pMediaLoaderModule: any
  ) {
    const p2pMediaLoaderOptions = options.p2pMediaLoader;
    const commonOptions = options.common;

    const trackerAnnounce = p2pMediaLoaderOptions.trackerAnnounce.filter((t) =>
      t.startsWith("ws")
    );

    const redundancyUrlManager = new RedundancyUrlManager(
      options.p2pMediaLoader.redundancyBaseUrls
    );

    const p2pMediaLoader: P2PMediaLoaderPluginOptions = {
      redundancyUrlManager,
      type: "application/x-mpegURL",
      startTime: commonOptions.startTime,
      src: p2pMediaLoaderOptions.playlistUrl,
    };

    let consumeOnly = false;
    // FIXME: typings
    if (
      navigator &&
      (navigator as any).connection &&
      (navigator as any).connection.type === "cellular"
    ) {
      consumeOnly = true;
    }


    const p2pMediaLoaderConfig: HlsJsEngineSettings = {

      loader: {

        trackerAnnounce,

        rtcConfig: getRtcConfig(),
        requiredSegmentsPriority: 1,
        segmentUrlBuilder: segmentUrlBuilderFactory(redundancyUrlManager, 1),
        useP2P: options.p2pMediaLoader.useP2P,
        localTransport: options.p2pMediaLoader.localTransport,
        consumeOnly,
      },
      segments: {
        swarmId: p2pMediaLoaderOptions.playlistUrl,
      },
    };

    if (!options.p2pMediaLoader.localTransport) {
      p2pMediaLoaderConfig.loader.segmentValidator = segmentValidatorFactory(
        options.p2pMediaLoader.segmentsSha256Url,
        options.common.isLive
      );
    }


    const hlsjs = {
      levelLabelHandler: (level: { height: number; width: number }) => {
        const resolution = Math.min(level.height || 0, level.width || 0);

        const file = p2pMediaLoaderOptions.videoFiles.find(
          (f) => f.resolution.id === resolution
        );
        // We don't have files for live videos
        if (!file) return level.height;

        let label = file.resolution.label;
        if (file.fps >= 50) label += file.fps;

        return label;
      },

      html5: {
        hlsjsConfig: {
          
          maxBufferHole: 0.5,
          maxFragLookUpTolerance: 0.25,
          highBufferWatchdogPeriod: 1,
          maxLoadingDelay : 2,
          lowLatencyMode: true,
          enableWorker: true,
          capLevelToPlayerSize: true,
          autoStartLoad: false,
          maxBufferLength: 30,
          maxMaxBufferLength: 600,
          maxBufferSize : 20 * 1000 * 1000,
          startFragPrefetch : true,

          nudgeMaxRetry : Infinity,

          loader: new p2pMediaLoaderModule.Engine(
            p2pMediaLoaderConfig
          ).createLoaderClass(),

        },
      },
    };

    const toAssign = { p2pMediaLoader, hlsjs };
    Object.assign(plugins, toAssign);

    return toAssign;
  }

  private static addWebTorrentOptions(
    plugins: VideoJSPluginOptions,
    options: PeertubePlayerManagerOptions
  ) {
    const commonOptions = options.common;
    const webtorrentOptions = options.webtorrent;

    const autoplay =
      this.getAutoPlayValue(commonOptions.autoplay) === "play" ? true : false;

    const webtorrent = {
      autoplay,
      videoDuration: commonOptions.videoDuration,
      playerElement: commonOptions.playerElement,
      videoFiles: webtorrentOptions.videoFiles,
      startTime: commonOptions.startTime,
    };

    Object.assign(plugins, { webtorrent });
  }

  private static getControlBarChildren(
    mode: PlayerMode,
    options: {
      pocketnetLink: string;
      logoType: string;
      theaterButton: boolean;
      captions: boolean;
      resolutionMenuButton : boolean;

      nextVideo?: Function;
      hasNextVideo?: () => boolean;

      previousVideo?: Function;
      hasPreviousVideo?: () => boolean;
    }
  ) {
    const settingEntries = [];
    const loadProgressBar =
      mode === "webtorrent" ? "peerTubeLoadProgressBar" : "loadProgressBar";

    // Keep an order
    settingEntries.push("playbackRateMenuButton");

    if (options.captions === true) settingEntries.push("captionsButton");
    if (options.resolutionMenuButton) settingEntries.push("resolutionMenuButton");

    const children = {};

    if (options.previousVideo) {
      const buttonOptions: NextPreviousVideoButtonOptions = {
        type: "previous",
        handler: options.previousVideo,
        isDisabled: () => {
          if (!options.hasPreviousVideo) return false;

          return !options.hasPreviousVideo();
        },
      };

      Object.assign(children, {
        previousVideoButton: buttonOptions,
      });
    }

    Object.assign(children, { playToggle: {} });

    if (options.nextVideo) {
      const buttonOptions: NextPreviousVideoButtonOptions = {
        type: "next",
        handler: options.nextVideo,
        isDisabled: () => {
          if (!options.hasNextVideo) return false;

          return !options.hasNextVideo();
        },
      };

      Object.assign(children, {
        nextVideoButton: buttonOptions,
      });
    }

    Object.assign(children, {
      
      progressControl: {
        children: {
          seekBar: {
            children: {
              [loadProgressBar]: {},
              mouseTimeDisplay: {},
              playProgressBar: {},
            },
          },
        },
      },

      p2PInfoButton: {},
      
      muteToggle: {},
      volumeControl: {},

      currentTimeDisplay: {},
      timeDivider: {},
      durationDisplay: {},
      liveDisplay: {},

      flexibleWidthSpacer: {},
      PictureInPictureBastyon : {},
      settingsButton: {
        setup: {
          maxHeightOffset: 40,
        },
        entries: settingEntries,
      },
    });
    

    if (options.pocketnetLink) {
      Object.assign(children, {
        pocketnetLinkButton: {
          href: options.pocketnetLink,
          logoType: options.logoType,
        },
      });
    }

    if (options.theaterButton === true) {
      Object.assign(children, {
        theaterButton: {},
      });
    }

    Object.assign(children, {
      fullscreenToggle: {
        handler: function (e: any) {
        },
      },
    });

    return children;
  }

  private static addContextMenu(
    mode: PlayerMode,
    player: videojs.Player,
    videoEmbedUrl: string,
    videoEmbedTitle: string
  ) {
    const content = () => {
      const isLoopEnabled = player.options_["loop"];
      const items = [
        {
          icon: "repeat",
          label:
            player.localize("Play in loop") +
            (isLoopEnabled ? '<span class="vjs-icon-tick-white"></span>' : ""),
          listener: function () {
            player.options_["loop"] = !isLoopEnabled;
          },
        },
        {
          label: player.localize("Copy the video URL"),
          listener: function () {
            copyToClipboard(buildVideoLink());
          },
        },
        {
          label: player.localize("Copy the video URL at the current time"),
          listener: function (this: videojs.Player) {
            copyToClipboard(buildVideoLink({ startTime: this.currentTime() }));
          },
        },
        {
          icon: "code",
          label: player.localize("Copy embed code"),
          listener: () => {
            copyToClipboard(
              buildVideoOrPlaylistEmbed(videoEmbedUrl, videoEmbedTitle)
            );
          },
        },
      ];

      if (mode === "webtorrent") {
        items.push({
          label: player.localize("Copy magnet URI"),
          listener: function (this: videojs.Player) {
            copyToClipboard(this.webtorrent().getCurrentVideoFile().magnetUri);
          },
        });
      }

      return items.map((i) => ({
        ...i,
        label: `<span class="vjs-icon-${i.icon || "link-2"}"></span>` + i.label,
      }));
    };

    // adding the menu
    //player.contextmenuUI({ content })
  }

  private static addHotkeysOptions(plugins: VideoJSPluginOptions) {
    const isNaked = (event: KeyboardEvent, key: string) =>
      !event.ctrlKey &&
      !event.altKey &&
      !event.metaKey &&
      !event.shiftKey &&
      event.key === key;

    Object.assign(plugins, {
      hotkeys: {
        skipInitialFocus: true,
        enableInactiveFocus: false,
        captureDocumentHotkeys: true,
        documentHotkeysFocusElementFilter: (e: HTMLElement) => {
          const tagName = e.tagName.toLowerCase();
          return (
            e.id === "content" || tagName === "body" || tagName === "video"
          );
        },

        enableVolumeScroll: false,
        enableModifiersForNumbers: false,

        rewindKey: function (event: KeyboardEvent) {
          return isNaked(event, "ArrowLeft");
        },

        forwardKey: function (event: KeyboardEvent) {
          return isNaked(event, "ArrowRight");
        },

        fullscreenKey: function (event: KeyboardEvent) {
          // fullscreen with the f key or Ctrl+Enter
          return (
            isNaked(event, "f") ||
            (!event.altKey && event.ctrlKey && event.key === "Enter")
          );
        },

        customKeys: {
          /*increasePlaybackRateKey: {
            key: function (event: KeyboardEvent) {
              return isNaked(event, ">");
            },
            handler: function (player: videojs.Player) {
              const newValue = Math.min(player.playbackRate() + 0.1, 5);
              player.playbackRate(parseFloat(newValue.toFixed(2)));
            },
          },
          decreasePlaybackRateKey: {
            key: function (event: KeyboardEvent) {
              return isNaked(event, "<");
            },
            handler: function (player: videojs.Player) {
              const newValue = Math.max(player.playbackRate() - 0.1, 0.1);
              player.playbackRate(parseFloat(newValue.toFixed(2)));
            },
          },*/
          frameByFrame: {
            key: function (event: KeyboardEvent) {
              return isNaked(event, ".");
            },
            handler: function (player: videojs.Player) {
              console.log("PA")
              player.pause();
              const dist = 1 / 30;
              player.currentTime(player.currentTime() + dist);
            },
          },
        },
      },
    });
  }

  private static getAutoPlayValue(autoplay: any) {
    if (autoplay !== true) return autoplay;

    // On first play, disable autoplay to avoid issues
    // But if the player already played videos, we can safely autoplay next ones
    if (isIOS() || isSafari()) {
      return PeertubePlayerManager.alreadyPlayed ? "play" : false;
    }

    return "play";
  }
}


