import videojs from "video.js";

const Button = videojs.getComponent("Button");
const MenuButton = videojs.getComponent("MenuButton");

export interface PocketnetButtonOptions extends videojs.ComponentOptions {
  href?: "";
  logoType?: "";
}

class PocketnetLinkButton extends MenuButton {
  voptions: PocketnetButtonOptions;
  link: HTMLElement;

  constructor(player: videojs.Player, options?: PocketnetButtonOptions) {
    console.log("options2", options);
    super(player, options);

    console.log(this);

    this.voptions = options || {};

    this.updateHref();
  }

  createEl() {
    return this.buildElement();
  }

  updateHref() {
    this.link.setAttribute("href", this.buildVideoLink());
    this.link.innerHTML = this.buildVideoLogo();
  }

  handleClick() {
    this.player().pause();
  }

  buildVideoLink() {
    return this.voptions.href || "https://pocketnet.app";
  }

  buildVideoLogo() {
    return "Watch on Bastyon"// this.voptions.logoType || "Pocketnet";
  }

  private buildElement() {
    const el = super.createEl();

    el.classList.add("pocketnet-link-button");
    this.link = videojs.dom.createEl("a", {
      href: "",
      innerHTML: "Pocketnet",
      title: this.player().localize("Video page (new window)"),
      className: "vjs-pocketnet-link",
      target: "_blank",
    }) as HTMLElement;

    el.appendChild(this.link);

    return el as HTMLButtonElement;
  }
}

videojs.registerComponent("pocketnetLinkButton", PocketnetLinkButton);
